import React, { Component } from 'react'
import moment from 'moment';
import { Space, Button, TimePicker, message } from 'antd'
import { UnorderedListOutlined } from '@ant-design/icons';
import '@/assets/scss/view-style/home.scss'
import work from '@/assets/images/work.svg'
import onwork from '@/assets/images/onwork.svg'
import offwork from '@/assets/images/offwork.svg'

// const onClick = (value) => console.log(value);
const format = 'HH : mm : ss'
const isPhone = /Mobi|Android|iPhone/i.test(navigator.userAgent) // 当前设备是移动设备

const getTime = (key) => {
  let val = localStorage.getItem(key);//获取存储的元素
  return val ? JSON.parse(val) : false

}
const setTime = (key, start, end) => {
  localStorage.setItem(key, JSON.stringify({ start, end }));//转换成json字符串序列
}
class Timer extends Component {
  state = {
    startTime: getTime('time').start || '08 : 30 : 00', // 上班时间
    endTime: getTime('time').end || '17 : 30 : 00', // 下班时间
    restTime: '00 : 00 : 00', // 剩余时间
    status: '', // 状态
  }
  // 生命周期componentWillMount
  componentWillMount() {
  }
  // 生命周期componentDidMount
  componentDidMount() {
    this.setTimer() // 计算剩余时间
  }
  // 生命周期componentWillUnmount
  componentWillUnmount() {
    clearInterval(this.timerID)
  }
  // 改变状态
  setStatus = (val) => {
    this.setState({
      status: val
    })
  }
  // 配置通知内容
  setNotification = (title, text) => {
    const sendNotification = () => { // 发送通知
      new Notification(title, {
        body: text,
        icon: require('@/assets/images/web-title.jpg')
      })
    }
    if (window.Notification.permission === "granted") { // 判断是否有权限
      sendNotification();
    } else if (window.Notification.permission !== "denied") {
      window.Notification.requestPermission((permission) => { // 没有权限发起请求
        sendNotification();
      });
    }
  }
  // 获取时间差
  getTimeDiff = (start, end) => {
    let second = Math.abs(start.diff(end, 'seconds') % 60).toString().padStart(2, '0'),
      minute = Math.abs(start.diff(end, 'minutes') % 60).toString().padStart(2, '0'),
      hour = Math.abs(start.diff(end, 'hours') % 24).toString().padStart(2, '0')
    return hour + ' : ' + minute + ' : ' + second
  }
  // 配置剩余时间与通知关系
  computeRestTime = () => {
    let start = moment(this.state.startTime, format),
      finish = moment(this.state.endTime, format),
      current = moment()
    if (current >= finish) {
      // 已下班
      this.setState({
        restTime: this.getTimeDiff(finish, current)
      })
      if (this.state.status === '1') {
        this.setNotification('重要通知：', '下班啦！') // 通知
        this.setStatus('2')
        return
      }
    } else if (current < start) {
      // 未上班
      this.setState({
        restTime: this.getTimeDiff(start, current)
      })
      if (this.status === '2') {
        this.setNotification('重要通知：', '新的一天开始了！') // 通知
        this.setStatus('0')
        return
      }
    } else if (current >= start && current < finish) {
      // 上班中
      this.setState({
        restTime: this.getTimeDiff(finish, current)
      })
      if (this.status === '0') {
        this.setNotification('重要通知：', '开始搬砖！') // 通知
        this.setStatus('1')
        return
      }
    }
  }
  // 计算剩余时间
  setTimer = () => {
    this.initStatus() // 初始化上班状态
    this.computeRestTime() // 处理定时任务
    this.timerID = setInterval(this.computeRestTime, 500)
  }
  // 初始化上班状态
  initStatus() {
    let start = moment(this.state.startTime, format),
      finish = moment(this.state.endTime, format),
      current = moment()
    if (current >= finish) {
      this.setNotification('现在状态：', '已下班！')
      this.setStatus('2')
    } else if (current < start) {
      this.setNotification('现在状态：', '尚未上班！')
      this.setStatus('0')
    } else if (current >= start && current < finish) {
      this.setNotification('现在状态：', '正在上班！')
      this.setStatus('1')
    }
  }
  // 保存
  handleSave = (val) => {
    clearInterval(this.timerID)
    this.setTimer()
    setTime('time', this.state.startTime, this.state.endTime)
    message.success('时间保存成功！');
  }
  // 取消
  handleCancel = () => {
    this.menuShow = false
  }
  // 改变开始时间
  onStartChange = (time, timeString) => {
    this.setState({
      startTime: timeString
    })
  }
  // 改变结束时节
  onFinishChange = (time, timeString) => {
    this.setState({
      endTime: timeString
    })
  }
  // 点击菜单按钮
  onClickMenu = () => {
    this.menuShow = !this.menuShow
  }
  render() {
    return (
      <Space direction="vertical" className={isPhone ? 'home-wrapper is-phone' : 'home-wrapper'}>
        <div className="time">
          {this.state.status === '0' ?
            <img className='image' src={onwork} alt='尚未上班' title='尚未上班！' /> :
            this.state.status === '1' ?
              <img className='image' src={work} alt='正在搬砖' title='正在搬砖！' /> :
              this.state.status === '2' ?
                <img className='image' src={offwork} alt='下班啦' title='下班啦！' />
                : <img className='image' src={work} alt='正在搬砖' title='正在搬砖！' />
          }
          <span className='text'>
            {this.state.restTime}
          </span>
        </div>
        {this.menuShow && <div className="editor">
          <TimePicker defaultValue={moment(this.state.startTime, format)} format={format} size="large" style={{ width: '100%' }} onChange={this.onStartChange} />
          <TimePicker defaultValue={moment(this.state.endTime, format)} format={format} size="large" style={{ width: '100%' }} onChange={this.onFinishChange} />
          <Space className='buttons'>
            <Button size="large" onClick={this.handleCancel}>关闭</Button>
            <Button size="large" type="primary" onClick={this.handleSave}>保存</Button>
          </Space>
        </div>}
        <UnorderedListOutlined className='icon-menu' onClick={this.onClickMenu} />
      </Space>
    )
  }
}

const HomeView = () => <Timer />
export default HomeView
